import { TitleCasePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { EmpexpenseserviceService } from 'src/app/core/services/empexpenseservice.service';
import { RolesPermissionsService } from 'src/app/core/services/roles-permissions.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import FileSaver from "file-saver";
import { saveAs } from "file-saver/dist/FileSaver";

@Component({
  selector: 'app-employee-approvals',
  templateUrl: './employee-approvals.component.html',
  styleUrls: ['./employee-approvals.component.scss']
})
export class EmployeeApprovalsComponent implements OnInit {
  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  public approvalslist: any;
  leavid: any;
  leavid1: any;
  public approved: boolean = false;
  public denied: boolean = false;
  public amount: any;
  public expenseId: any;
  public searchValueAmount = '';
  DataLink: any;
  public searchKey: any;

  constructor(public rolesPermissionsService: RolesPermissionsService, private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe, public expenseservice: EmpexpenseserviceService) { }

  ngOnInit(): void {
    this.getUsersList();
  }

  getUsersList() {
    this.rolesPermissionsService.getemployeeApproval({}, this.currentUser.id).then((res) => {

      this.approvalslist = res.data;
      console.log("res amount approvals", this.approvalslist)
    })

  }
  getexpenseSearch(searchKey) {
    if (this.searchKey == "") {
      this.toastService.toastMsg({
        title: "Error",
        content: "Please Enter Valid Employee Name!!!",
      });
      // this.toastService.openErrorSnackBar("Please Enter Valid Employee Name!!!");
      this.rolesPermissionsService.getExpenseApprovalsearch({}, this.currentUser.id, searchKey).then((res) => {
        if (res) {
          this.approvalslist = res.data;
        }
      })

    }
    else {
      this.rolesPermissionsService.getExpenseApprovalsearch({}, this.currentUser.id, searchKey).then((res) => {
        if (res) {
          this.approvalslist = res.data;
        }

      }, (err) => {
        if (err.error.expose) {
          this.toastService.toastMsg({
            title: "Error",
            content: this.titleCasePipe.transform(err.error.error_message),
          });
          // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else {
          this.toastService.toastMsg({
            title: "Error",
            content: "Something Went Wrong.",
          });
          // this.toastService.openErrorSnackBar("Something Went Wrong.");
        }
      })

    }

  }

  onSearch(event) {

    if (event.target.value !== '') {
      this.searchValueAmount = event.target.value;
      console.log("search value", this.searchValueAmount)
    }
    else {
      this.searchValueAmount = event.target.value;
      console.log("empty search value", this.searchValueAmount)
      this.toastService.toastMsg({
        title: "Error",
        content: "Add Employee Expense Approval Amount!!!",
      });
      // this.toastService.openErrorSnackBar("Add Employee Expense Approval Amount !!!");
    }
  }

  getexpenseId(expenseId) {
    this.expenseId = expenseId

  }

  ontrueclick(leavid) {

    console.log(" this.leavid", this.leavid);
    //  this.approved = true;
    // this.denied = false;

    if (this.searchValueAmount == '') {
      this.toastService.toastMsg({
        title: "Error",
        content: "Add Employee Expense Approval Amount!!!",
      });
      // this.toastService.openErrorSnackBar("Add Employee Expense Approval Amount !!!");
    }
    else {
      this.leavid = leavid;
      let body =
      {
        "status": "ACCEPTED",
        "approvedAmount": this.searchValueAmount
      }

      this.rolesPermissionsService.approvalstatusById(body, this.currentUser.id, this.leavid).then((res) => {
        if (res.success === true) {

          // this.toastService.openSnackBar("Employee Expense Approval Successfully!!!");
          this.toastService.toastMsg({
            title: "Error",
            content: "Employee Expense Approval Successfully!!!",
          });
          this.getUsersList();
          this.searchValueAmount = '';
          this.approved = true;
          this.denied = false;
        }

      }, (err) => {
        if (err.error.expose) {
          this.toastService.toastMsg({
            title: "Error",
            content: this.titleCasePipe.transform(err.error.error_message),
          });
          // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else {
          this.toastService.toastMsg({
            title: "Error",
            content: "Something Went Wrong.",
          });
          // this.toastService.openErrorSnackBar("Something Went Wrong.");
        }

      });
    }

  }

  onclose(id) {

    for (var i = 0; i < this.approvalslist.length; i++) {
      if (this.approvalslist[i].id == id) {

        {
          this.approvalslist[i].status = "PENDING"
          this.approved = false;
        }
      }
    }
    console.log("this.approvalslist", this.approvalslist)

  }

  onfalseclick(leavid) {
    console.log(" this.leavid", this.leavid1);
    this.leavid1 = leavid;
    let body1 =
    {
      "status": "REJECTED"
    }

    this.rolesPermissionsService.approvalstatusById(body1, this.currentUser.id, this.leavid1).then((res) => {
      if (res) {
        this.toastService.toastMsg({
          title: "Success",
          content: "Employee Expense Approval Denied Successfully!!!",
        });
        // this.toastService.openSnackBar("Employee Expense Approval Denied Successfully!!!");

        this.getUsersList();
        this.searchValueAmount = '';
        this.approved = false;
        this.denied = true;
      }

    }, (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }

    });
    // }

  }

  deletExpenseById() {
    this.expenseservice.deleteExpense({}, this.expenseId).then(res => {
      this.toastService.toastMsg({
        title: "Success",
        content: "Employee Expense Approval Deleted Successfully!!!",
      });
      // this.toastService.openSnackBar("Employee Expense Approval Deleted Successfully")
      this.getUsersList();

    }, (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })

  }

  GotoImageOrPDF(imagelink) {
    this.DataLink = imagelink
    const pdfName = "document.pdf";
    FileSaver.saveAs(this.DataLink, pdfName);


  }
} 
