import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { EmployeeComponent } from './employee.component';
import { EmployeeRolesComponent } from '../Employees/employee-roles/employee-roles.component';
import { EmployeeDetailsComponent } from './employee-details/employee-details.component';
import { AddRoleBasedUsersComponent } from '../add-role-based-users/add-role-based-users.component';
import { EmployeeApprovalsComponent } from './employee-approvals/employee-approvals.component';

const routes: Routes = [
  { 
    path: '',
    component : EmployeeComponent,
    children : [
      { 
        path: '', 
        redirectTo: 'allEmployees',
        pathMatch: 'full'
      },
      { 
        path: "allEmployees",
        component: EmployeeRolesComponent
      },
      { 
        path: "employee-approvals",
        component: EmployeeApprovalsComponent
      },
      {
        path: "viewEmployee/:id",
        component: EmployeeDetailsComponent
      },
      {
        path: "add-roleUsers",
        component: AddRoleBasedUsersComponent
      },
    ] ,
    pathMatch: 'prefix',
    data: {
      routerLinkActiveOptions: { exact: true, pathMatch: 'full' }
    }
  },
  
];


@NgModule({
  declarations: [],
  imports: [RouterModule.forChild(routes)],

})
export class EmployeeRoutingModule { }
